import React, { createContext, useEffect, useState } from "react";
import localForage from "localforage";
import axios from "axios";
import { VideoType } from "./video-types";

interface ContextVal {
  videos: VideoType[];
}

export const GlobalContext = createContext<Partial<ContextVal>>({});

export function GlobalContextProvider({ children }: { children: any }) {
  const [videos, setVideos] = useState<VideoType[]>([]);

  useEffect(() => {
    getYoutubeVideos().then((vids) => {
      if (vids) setVideos(vids);
      //   console.log(vids);
    });
  }, []);

  return (
    <GlobalContext.Provider value={{ videos }}>
      {children}
    </GlobalContext.Provider>
  );
}
const debugStore = localForage.createInstance({ name: "debugstore" });

async function getYoutubeVideos(): Promise<VideoType[] | undefined | []> {
  if (process.env.NODE_ENV === "production") {
    let apikey = "AIzaSyBlqsRM4RuYF5Xe13wySA11TWn84TJiSN8";

    var videosUrl = `https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&maxResults=50&playlistId=PLp1LthaVYwB1qNCOeTXJL2TIP0q-oQcoW&key=${apikey}`;
    // var videosUrl = `https://www.googleapis.com/youtube/v3/search?channelId=${channelID}&order=date&part=snippet&type=video&maxResults=10&key=${apikey}`;
    // var videosUrl = `https://www.googleapis.com/youtube/v3/search?channelId=UCEjTjW7OVRaT0M1e761hfKw&order=date&part=snippet&type=video&maxResults=10&key=AIzaSyD16h4hSvH16RBC99E5QAUaLD1fFHrD_rQ`;
    //   let data = await axios.get(videosUrl).catch((e) => console.log(e));
    // console.log(await debugStore.getItem("videos"));
    const videos: Partial<{
      items: VideoType[] | undefined | [];
    } | null> = (await axios.get(videosUrl)).data;

    if (videos) {
      return videos.items;
    }
    return [];
  }
  const videos: Partial<{
    items: VideoType[] | undefined | [];
  } | null> = await debugStore.getItem("videos");

  if (videos) {
    return videos.items;
  }
  return [];
}
