function ResponsiveDialog() {
  const [open, setOpen] = React.useState("");
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
  const history = useHistory();
  const location = useLocation();
  const [video, setVideo] = useState<VideoType>();
  const [loading, setLoading] = useState(true);

  const { videos } = useContext(GlobalContext);

  useEffect(() => {
    if (open && videos) {
      setVideo(
        videos.filter((vid) => vid.snippet.resourceId.videoId === open)[0]
      );
    }
  }, [videos, open]);
  const handleClose = () => {
    setOpen("");
    setLoading(true);
    history.push("/");
  };

  useEffect(() => {
    const searchParams = new URLSearchParams(location.search);
    const videoId = searchParams.get("videoId");
    if (videoId) {
      setOpen(videoId);
    } else {
      setOpen("");
    }
  }, [location]);

  const [copied, setCopied] = useState(false);

  async function share() {
    copyTextToClipboard(window.location.href);
    setCopied(true);
  }

  return (
    <Dialog
      fullScreen={fullScreen}
      open={!!open}
      scroll="body"
      onClose={handleClose}
      PaperProps={{ style: { background: "var(--background)" } }}
      aria-labelledby="responsive-dialog-title"
    >
      <DialogActions>
        <Button style={{ color: "white" }} onClick={handleClose}>
          close
        </Button>
        <Button onClick={share}>
          <ShareOutlinedIcon color="primary" />
        </Button>
      </DialogActions>
      <CopiedToClipboard open={copied} set={setCopied} />
      {loading ? <LinearProgress /> : <></>}
      <div className="player">
        <div className="youtube">
          <YouTube
            onReady={() => {
              setLoading(false);
            }}
            containerClassName="iframeContainer"
            videoId={video?.snippet.resourceId.videoId}
          />
        </div>
      </div>
      <div className="video-details">
        <p className="title">{video?.snippet.title}</p>
        <div className="subtitle">
          {video?.snippet.description.split("\n").map((line, i) => {
            return <p key={i}>{line}</p>;
          })}
        </div>
      </div>
    </Dialog>
  );
}

function CopiedToClipboard({
  set,
  open,
}: {
  set: React.Dispatch<React.SetStateAction<boolean>>;
  open: boolean;
}) {
  useEffect(() => {
    if (open) {
      setTimeout(() => {
        set(false);
      }, 2000);
    }
    // eslint-disable-next-line
  }, [open]);

  return (
    <Collapse in={open}>
      <span
        style={{
          display: "block",
          color: "var(--accent)",
          textAlign: "right",
          padding: "10px",
        }}
        className="alert-text"
      >
        Copied link
      </span>
    </Collapse>
  );
}
