function ViewVideoPage({ playing }: { playing: string }) {
  const [video, setVideo] = useState<VideoType>();

  const { videos } = useContext(GlobalContext);

  const ref = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    if (playing && videos && videos.length) {
      // ref.current?.scrollIntoView({ behavior: "smooth" });
      setVideo(
        videos.filter((vid) => vid.snippet.resourceId.videoId === playing)[0]
      );
      window.scrollTo({ top: 0, behavior: "smooth" });
    }
  }, [playing, videos]);

  return (
    <Collapse in={!!playing}>
      <div ref={ref} className="player">
        <YouTube containerClassName="bigplayer" videoId={playing} />
        <div className="video-details">
          <p className="title">{video?.snippet.title}</p>
          <div className="subtitle">
            {video?.snippet.description.split("\n").map((line, i) => {
              return <p key={i}>{line}</p>;
            })}
          </div>
        </div>
      </div>
    </Collapse>
  );
}

