function getThumbnail(
  video: VideoType
): { mq: string; maxhd: string; sd: string } {
  return {
    mq: `http://img.youtube.com/vi/${video.snippet.resourceId.videoId}/mqdefault.jpg`,
    maxhd: `http://img.youtube.com/vi/${video.snippet.resourceId.videoId}/maxresdefault.jpg`,
    sd: `http://img.youtube.com/vi/${video.snippet.resourceId.videoId}/sddefault.jpg`,
  };
}
function Video({
  video,
  index,
}: {
  video: VideoType;
  animstyle?: any;
  index: number;
}) {
  const [state, setState] = useState("idel");
  const [showdetails, setShowdetails] = useState(false);
  const [style, set] = useSpring(() => {
    return { opacity: 0, y: 100 };
  });

  useEffect(() => {
    set({ opacity: 1, y: 0, delay: 1500 + index * 100 });
  }, [set, index]);

  const history = useHistory();

  return (
    <>
      <animated.div
        // @ts-ignore
        style={{ ...style, position: index === 0 ? "relative" : "" }}
        className="work-video"
        onClick={() => {
          history.push("/?videoId=" + video.snippet.resourceId.videoId);
        }}
        onMouseEnter={() => setShowdetails(true)}
        onMouseLeave={() => setShowdetails(false)}
      >
        {video && state ? (
          <img src={getThumbnail(video).mq} alt="" className="thumbnail" />
        ) : (
          <></>
        )}
        <div className="details" style={{ opacity: Number(showdetails) }}>
          <p className="title">{video.snippet.title}</p>
        </div>
      </animated.div>
    </>
  );
}

export default Home;
function copyTextToClipboard(text: string) {
  var textArea = document.createElement("textarea");
  textArea.value = text;
  document.body.appendChild(textArea);
  textArea.focus();
  textArea.select();

  try {
    var successful = document.execCommand("copy");
    var msg = successful ? "successful" : "unsuccessful";
    console.log("Copying text command was " + msg);
  } catch (err) {
    console.log("Oops, unable to copy");
  }

  document.body.removeChild(textArea);
}
